.auth-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.auth-container .auth-card {
    background-color: white;
    z-index: 100;
    border: 1px solid #bfbfbf;
    padding: 16px;
    border-radius: 10px;
    box-shadow: 3px 3px 10px 0px rgba(0,0,0,0.25);
}

.auth-container .auth-card img {
    width: auto;
    height: 80px;
    margin-left: 100px;
    border-radius: 50%;
    margin-bottom: 20px;
}

