input.input {
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #2196f3;
    outline: none;
    display: block;
    margin-bottom: 12px;
    min-width: 300px;
    color: #2196f3;
}

button.btn {
    padding: 16px;
    border: 1px solid #2196f3;
    border-radius: 8px;
    min-width: 200px;
    background: #2196f3;
    color: white;
    display: block;
    margin:0 auto;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
    outline: none;
    margin-bottom: 10px;
    }